import { __decorate } from "tslib";
import { Vue, Component, Mixins } from 'vue-property-decorator';
import vClickOuside from 'v-click-outside';
import MessageBox from '@/components/MessageBox.vue';
import RowBack from '@/components/RowBack.vue';
import MsgBoxToolsApp from '@/mixins/MsgBoxToolsApp';
import animateIfVisible from '@/mixins/animateIfVisible';
import { newsMapper } from '../module/store';
import CardNews from '../components/CardNews.vue';
import MsgBoxTools from '../mixins/MsgBoxTools';
import ButtonApp from '@/components/ButtonApp.vue';
const NewsMappers = Vue.extend({
    computed: {
        ...newsMapper.mapState(['currentNews']),
        ...newsMapper.mapGetters(['newsById'])
    },
    methods: {
        ...newsMapper.mapMutations(['clearCurrentNews']),
        ...newsMapper.mapActions(['getCurrentNews', 'publishNews', 'deleteNews'])
    }
});
let PageNews = class PageNews extends Mixins(MsgBoxTools, MsgBoxToolsApp, NewsMappers) {
    constructor() {
        super(...arguments);
        this.secondBtn = null;
    }
    get fetchListFailed() { return this.requestStatus === 'failFetchList'; }
    get currentId() { return this.$route.params.id; }
    // HOOKS
    created() {
        this.getData(false);
        document.addEventListener('keydown', this.keydownHandler);
    }
    async mounted() {
        await this.$nextTick();
        animateIfVisible();
    }
    beforeDestroy() {
        document.removeEventListener('keydown', this.keydownHandler);
        this.clearCurrentNews();
    }
    goToPageMain() { this.$router.push({ name: 'PageNews' }).catch(err => { }); }
    // IDENTITY DATA METHODS
    getData(forceUpdate) {
        this.getCurrentNews({ id: Number(this.currentId), forceUpdate })
            .catch(() => {
            this.requestStatus = 'failFetchCurrentNews';
            this.secondBtn = { type: 'success', isPlain: true };
            this.openMsgBox();
            return;
        });
    }
    submitPublish() {
        this.publishNews(Number(this.currentId))
            .then(() => {
            this.getData(true);
        })
            .catch(() => {
            this.requestStatus = 'failPublish';
            this.secondBtn = { type: 'danger', isPlain: true };
            this.openMsgBox();
            return;
        });
    }
    submitDelete() {
        this.deleteNews(Number(this.currentId))
            .then(() => {
            this.getData(true);
        })
            .catch(() => {
            this.requestStatus = 'failPublish';
            this.secondBtn = { type: 'danger', isPlain: true };
            this.openMsgBox();
            return;
        });
    }
    // CLICK HANDLERS
    onFirstBtnClick() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'failFetchCurrentNews':
                this.getData(false);
                break;
            case 'failPublish':
                this.submitPublish();
                break;
            case 'failDelete':
                this.submitDelete();
                break;
        }
    }
    onSecondBtnClick() {
        this.closeMsgBox();
        switch (this.requestStatus) {
            case 'failFetchCurrentNews':
                this.goToPageMain();
                break;
            case 'failPublish':
            case 'failDelete':
                break;
        }
    }
    // OTHER HANDLERS
    keydownHandler(evt) {
        if (evt.key === 'Escape') {
            if (this.msgBoxIsShown)
                this.closeMsgBox();
        }
    }
    onClickOutside(evt) {
        const targetIsPage = evt.srcElement.classList.contains('page-item');
        if (targetIsPage)
            this.goToPageMain();
    }
};
PageNews = __decorate([
    Component({
        directives: {
            clickOutside: vClickOuside.directive
        },
        components: {
            CardNews,
            MessageBox,
            RowBack,
            ButtonApp
        }
    })
], PageNews);
export default PageNews;
